@import url('https://fonts.googleapis.com/css?family=Open+Sans&display=swaphttps://fonts.googleapis.com/css?family=Raleway');
@import url('https://fonts.googleapis.com/css2?family=Comforter&family=Satisfy&display=swap');

/*main*/
.main-about-hero {
  width: 100%;
  height: 90vh;
  margin-bottom: 0;
  padding-top: 0;
  padding-bottom: 0;
  cursor: none;
}

/*text*/
.text-about-section p {
  color: white;
  font-size: 1.6em;
  width: 90vw;
  text-align: justify;
}

.motto-text { 
  font-size: 2.5em;
  font-family: 'Comforter';
  position: absolute;
  color: white;
  margin-left: -30%;
  margin-top: 43%;
}

/*top-side*/
.r-side {
  padding: 0;
  margin: 0;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 50vh;
  background-color: #111820;
}

/*image*/
.image-about-section {
  width: 50%;
  position: relative;
  text-align: center;
}

.image-about {
  height: 600px;
}
 
/*left side*/
.l-side {
  position: relative;
  display: flex;
  justify-content: center;
  text-align: center;
  align-items: center;
  padding-top: 20px;
  padding-bottom: 20px;
  margin: 0;
  width: 100%;
  height: 50vh;
  background-color: #19322f;  
}

/*big screens*/
@media (min-width: 1901px) {
  .r-side {
    height: 50vh;
  }

  .l-side {
    height: 40vh;
  }
}

 @media (min-width: 1281px) and (max-width: 1900px) {

  .r-side {
    height: 80vh;
  }

  .left-side {
    height: 75vh;
  }

  .motto-text { 
    margin-left: -30%;
    margin-top: 55%;
  }

  .text-about-section {
    padding-top: 35px;
    font-size: 1rem;

  }
 }
 
    @media screen and (max-width: 768px) {
      .main-about-hero {
        width: 100%;
        height: 100vh;

      }
      .l-side {
        width: 100%;
      }

      .r-side {
        
        width: 100%;
      }

    }


