@import url('https://fonts.google.com/specimen/Nunito?category=Sans+Serif');
@import url('https://fonts.googleapis.com/css?family=Open+Sans&display=swaphttps://fonts.googleapis.com/css?family=Raleway');

.main-hero{
  width: 100%;
  height: 90vh;
  margin-bottom: 0;
  padding-top: 0;
  padding-bottom: 0;
}

.hero-container {
  background-image: linear-gradient(to bottom right,rgb(14, 14, 22),rgb(14, 14, 22),rgb(14, 14, 22),rgb(14, 14, 22), rgb(24, 24, 37),rgb(35, 52, 59),rgb(64, 55, 71), rgb(139, 80, 70), rgb(57, 63, 87));
  position: relative;
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  height: 90vh;
  max-width: 100%;
  padding-right: 0;
  padding-bottom: 0;
  padding-left: 0;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
  -webkit-flex-direction: row;
  -ms-flex-direction: row;
  flex-direction: row;
  -webkit-box-pack: justify;
  -webkit-justify-content: space-between;
  -ms-flex-pack: justify;
  justify-content: space-between;
  -webkit-flex-wrap: wrap;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  -webkit-align-content: flex-start;
  -ms-flex-line-pack: start;
  align-content: flex-start;
  
}
/*left container*/
.left-container {
  position: relative;
    left: auto;
    top: 10%;
    z-index: 7;
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    max-width: 50%;
    margin-right: 20px;
    margin-left: 5%;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -webkit-flex-direction: column;
    -ms-flex-direction: column;
    flex-direction: column;
    -webkit-box-pack: start;
    -webkit-justify-content: flex-start;
    -ms-flex-pack: start;
    justify-content: center;
    -webkit-box-align: start;
    -webkit-align-items: flex-start;
    -ms-flex-align: start;
    align-items: center;
    -webkit-align-self: flex-start;
    -ms-flex-item-align: start;
    align-self: flex-start;
    -webkit-box-ordinal-group: 0;
    -webkit-order: -1;
    -ms-flex-order: -1;
    order: -1;
    -webkit-box-flex: 1;
    -webkit-flex: 1;
    -ms-flex: 1;
    flex: 1;
}
  /*social*/
  .social-icon-link {
    color: #C2A43B;
    font-size: 25px;
    border-radius: 55px;
    border: 2px solid #C2A43B;
    z-index: 996;
  }
  
  .social-icons {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 300px;
    margin-top: 40px;
    z-index: 999;
  }

  .social-icon-link .fab, .fas {
    margin: 15px;
    color: #C2A43B;
  }
  
