.text {
  width: 100%;
  font-family: 'Raleway';
  font-weight: 100;
  font-size: 28px;
  color: white;
  display: block;
  text-align: center;
  animation: showBottomText 0.5s;
  animation-delay: 0.7s;
  animation-fill-mode: forwards;
  top: 0;
  transform: translate(0, -100%);
}

.text-title{
  font-family: 'Raleway', sans-serif;
  font-weight: 700;
  font-size: 50px;
  color:  #C2A43B;
  display: block;
  text-align: center;
  animation: showTopText 1s;
  animation-delay: 0.5s;
  animation-fill-mode: forwards;
  bottom: 0;
  transform: translate(0, 100%);
}

/*animation*/
@keyframes showTopText {
  0% { transform: translate3d(0, 100%, 0); }
  100% { transform: translate3d(0, 0, 0); }
}

@keyframes showBottomText {
  0% { transform: translate3d(0, -100%, 0); }
  100% { transform: translate3d(0, 0, 0); }
}


/*right container*/
.image-container {
  position: relative;
    z-index: 100;
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    width: 50%;
    height: 90vh;
    max-width: 55%;
    margin-top: 0;
    margin-right: auto;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -webkit-flex-direction: column;
    -ms-flex-direction: column;
    flex-direction: column;
    -webkit-box-pack: end;
    -webkit-justify-content: flex-end;
    -ms-flex-pack: end;
    justify-content: flex-end;
    -webkit-box-align: end;
    -webkit-align-items: flex-end;
    -ms-flex-align: end;
    align-items: flex-end;
  /*float: right;
  width: 50%;
  height: 100%;
  overflow: hidden;
  display: flex;
  position: absolute;*/
}

/*mountain*/
.mountain {
  position: absolute;
  left: auto;
  top: auto;
  right: 0;
  bottom: 0;
  z-index: 3;
  width: 850px;
  height: auto;
}
.image-mountain {
  border: 0;
  max-width: 100%;
  vertical-align: middle;
  display: inline-block;
}

/*sun*/
.sun{
  position: absolute;
    left: auto;
    top: auto;
    right: 0;
    bottom: 0;
    z-index: 2;
    width: 250px;
    overflow: auto;
}

/*.image-sun{
  margin-right: 35%;
}*/

.sun-beam{
  position: absolute;
  z-index: 4;
  right: 27%;
  bottom: 20%;
}

.image-beam {
  width: 100px;
  height: 100px;
  border: 0;
  vertical-align: middle;
  display: inline-block;
}

/*big screens*/
@media (min-width: 1901px) {
  .image-mountain {
      width: 95vw;
      height: 70vh;
    }

    .mountain {
      width: 85vw;
      height: 70vh;
    }

    .image-sun {
      z-index: 4; 
      width: 700px;
      height: 500px;
    }

    .sun {
      bottom: 10%;
      left: 25%;
    }

    .sun-beam{
      z-index: 5;
      position: absolute;
      bottom: 25%;
      left: 55%;
    }

    .text {
      font-family: 'Raleway';
      font-weight: 100;
      font-size: 3em;
      color: white;
      display: block;
      text-align: center;
    }
    
    .text-title{
      font-family: 'Raleway', sans-serif;
      font-weight: 700;
      font-size: 5em;
      color:  #C2A43B;
      display: block;
      text-align: center;
    }

    .social-icon-link {
      color: #C2A43B;
      font-size: 35px;
      border-radius: 55px;
      border: 3px solid #C2A43B;
    }

   .left-container {
     margin-top: 0;
     z-index: 999;
   }
    
  }

@media (max-width:768px) {
.hero-container {
  background-image: linear-gradient(to bottom right,rgb(14, 14, 22),rgb(14, 14, 22),rgb(14, 14, 22),rgb(14, 14, 22), rgb(24, 24, 37),rgb(35, 52, 59),rgb(64, 55, 71), rgb(57, 63, 87));
  width: 100%;
  flex-direction: row;
  }

.image-container {
    display: none;
  }

.left-container{
    width: 100%;
    text-align: center;
    justify-content: center;
    align-items: center;
  }

.text {
  font-size: 18px;
  width: 100%;
}

.text-title{
  font-size: 35px;
  width: 100%;
}

.social-icon-link {
  color: #C2A43B;
  font-size: 15px;
  border-radius: 55px;
  border: 2px solid #C2A43B;
}

.social-icons {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin-top: 50px;

}
}
  
