@import url('https://fonts.googleapis.com/css?family=Open+Sans&display=swaphttps://fonts.googleapis.com/css?family=Raleway');
*{
    margin: 0;
}
.works-section-home {
  margin: 0;
  padding: 0;
  background-color: #1A1C28;
  height: 100%;
  align-content: center;
  padding: 5%;
  padding-left: 20%;
  padding-right: 15%;
  cursor: none;
}

.works-section-home .container {
    justify-content: center;
}

.works-title-home h1 {
  font-size: 3rem;
  font-family: 'Raleway', sans-serif;
  font-weight: 700;
  
}

.text-container {
  display: block;
  margin-bottom: 10%;
  margin-top: 10%;
  justify-content: center;
}

/*text box*/
.text-box {
  margin: 20px;
  padding: 20px;
  width: 100%;
}
.text-box p{
  color: white;
  font-family: 'Raleway', sans-serif;
  font-weight: 200;
  padding: 5px;
  font-size: 1.5rem;
}

.works-title-home h1{
  color: white;
  font: 2.5rem;
}

.text-box h3{
  color: white;
  font-family: 'Raleway', sans-serif;
  font-weight: 600;
}

.text-box h2{
  color: #C1AB65;
  font-family: 'Raleway', sans-serif;
  font-weight: 600;
  font-size: 2rem;
  margin-top: 20px;
  margin-bottom: 50px;
}

/*visual*/
.image-container-works {
  width: 100%;
  justify-content: center;
  align-items: center;
  display: flex;
}
.video-container-works {
  width: 100%;
  height: auto;
}

.text-container .text-box h3 {
  font-size: 20px;
  text-transform: uppercase;
  margin-bottom: 10px;
}

/*container*/
.container-first {
  width: 65vw;
  background-color: #0E0E16;
  height: auto;
}
.container {
    width: 65vw;
    background-color: #0E0E16;
    opacity: 25%;
    height: auto;
}

.works-links {
  margin-top: 5%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 200px;
  height: auto;  
}

.links-works {
  color: whitesmoke;
}

.links-works:hover {
  border-bottom: 2px solid #C2A43B;
  transition: all 0.2s ease-out;
}

/*image*/
.image-works-nlnw{
    height: 40vh;
    width: 80%;
    padding-bottom: 25px;
    padding-top: 25px;
}

.image-works-ta{
  height: 30vh;
  padding-bottom: 25px;
  padding-top: 25px;
}

/*line*/
.line-works{
 border: 5px solid #1A1C28;
}

/*animation*/
@keyframes fade-bottom {
  0% {
    transform: translateY(50px);
    opacity: 0;
  }
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}

/*media*/

@media (max-width: 900px) {
    .works-title-home h1 {
    font-size: 2rem;
    text-align: center;
  }
   .text-container {
    flex-direction: column;
  }
}

.reveal {
  position: relative;
  opacity: 0;
}

.reveal.active {
  opacity: 1;
}
.active.fade-bottom {
  animation: fade-bottom 1s ease-in;
}

@media (max-width:768px) {
  .works-title-home h1 {
  font-size: 1.5rem;
  text-align: center;
}
 .image-container-works {
   height: 200px;
 }

}



