.works-main-links {
  position: absolute;
  display: flex;
  justify-content: space-between;
  width: 200px;
  height: 100px; 
  left: 0;
  padding-left: 30px;
}

.links-works {
  color: whitesmoke;
}

.links-works:hover {
  border-bottom: 2px solid #C2A43B;
  transition: all 0.2s ease-out;
}

@media (max-width:768px) {
  .image-wave{
    display: none;
  }
.tabs-split {
