@import url('https://fonts.googleapis.com/css?family=Open+Sans&display=swaphttps://fonts.googleapis.com/css?family=Raleway');

.footer-container {
    background-color: #0e1417;
    padding: 4rem 0 2rem 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    z-index: 999;
    bottom: 0;
  }
  
  .footer-subscription {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    margin-bottom: 24px;
    padding: 24px;
    color: #fff;
  }

  .footer-logo{
    color: white;
    font-size: 15px;
    font-family: 'Raleway', sans-serif;
    font-weight: 100;
  }
   
  .footer-subscription-heading {
    font-family: 'Raleway', sans-serif;
    font-weight: 700;
    margin-bottom: 24px;
    font-size: 2.5rem;
    color: white;
    padding-bottom: -15px;
  }

  /* Social Icons */
  .social-icon-link-footer {
    color: white;
    font-size: 35px;
    cursor: pointer;
  }
  
  .social-media {
    max-width: 1000px;
    width: 100%;
  }
  
  .social-media-wrap {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 90%;
    max-width: 1000px;
    margin: 5px auto 0 auto;
  }
  
  .social-icons-footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 300px;
  }
  
  .social-logo {
    color: #fff;
    justify-self: start;
    margin-left: 20px;
    cursor: pointer;
    text-decoration: none;
    font-size: 2rem;
    display: flex;
    align-items: center;
    margin-bottom: 16px;
  }
  
  .website-rights {
    color: #fff;
    margin-bottom: 16px;
  }
  
  @media screen and (max-width: 820px) {
    .footer-links {
      padding-top: 2rem;
    }
  
    .btn {
      width: 100%;
    }
  
    .footer-link-wrapper {
      flex-direction: column;
    }
  
    .social-media-wrap {
      flex-direction: column;
    }
  }
  
  @media screen and (max-width: 768px) {
  }