@import url('https://fonts.googleapis.com/css?family=Open+Sans&display=swaphttps://fonts.googleapis.com/css?family=Raleway');

.home-works {
  margin: 0;
  background-color: #1a2126;
  height: auto;
  align-content: center;
  padding: 5%;
}

.container-tab-works {
    display: flex;
    flex-direction: column;
    position: relative;
    width: 75%;
    height: 500px;
    background: #1f282f;
    margin: 100px auto 0;
    word-break: break-all;
    border: 1px solid rgba(0, 0, 0, 0.274);
  }
 
  .bloc-tabs {
    display: flex;
  }
  .tabs {
    padding: 15px;
    text-align: center;
    width: 50%;
    background: rgba(128, 128, 128, 0.075);
    cursor: pointer;
    border-bottom: 1px solid rgba(0, 0, 0, 0.274);
    box-sizing: content-box;
    position: relative;
    outline: none;
  }

  .tabs-split {
    display: flex;
    justify-content: space-between;
  
    
  }
  .tabs:not(:last-child){
    border-right: 1px solid rgba(0, 0, 0, 0.274);
  }
  
  .active-tabs  {
    background: #393f57;
    border-bottom: 1px solid transparent;
  }
  
  .active-tabs::before {
    content: "";
    display: block;
    position: absolute;
    top: -5px;
    left: 50%;
    transform: translateX(-50%);
    width: calc(100% + 2px);
    height: 5px;
    background: #C1AB65;
  }
/*tabs*/
  button {
    border: none;
    font-family: 'Raleway';
    font-weight: 400;
    color: white;

  }
  .content-tabs {
    flex-grow : 1;
  }
  .content {
    background-color: #1f282f;
    width: 100%;
    height: 100%;
    display: none;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 1), 0 6px 10px 0 rgba(0, 0, 0, 1);
    position: relative;
  }

  /*text*/
  .home-works h1 {
    font-size: 3.5rem;
    color: white;
    font-family: 'Raleway';
    font-weight: 700;
    text-align: center;
  }

  .tabs-header{
    margin-left: 25px;
    padding-top: 30px;
    width: 50%;
    font-family: 'Raleway';
    font-weight: 700;
    color:  #C2A43B;
  }
 
  .tabs-explanation {
    width: 65%;
    height: 100%;
    color: white;
    padding: 25px;
    text-align: justify;
    font-family: 'Raleway';
    font-weight: 200;
    font-size: 1.3em;
    margin-top: 20px;
  }

  .tabs-explanation-aino {
    width: 55%;
    height: 100%;
    color: white;
    padding: 25px;
    text-align: justify;
    font-family: 'Raleway';
    font-weight: 200;
    font-size: 1.3em;
    margin-top: 20px;
  }
  .active-content {
    display: block;
  }

  /*images*/
  .image-app{
    height: 400px;
    float: right;
    padding: 15px;
    padding-right: 5%;
    z-index: 1;
}

.image-aino{
  height: 400px;
  float: right;
  padding: 15px;
  padding-right: 2%;
  z-index: 1;
}

/*bottom*/
.works-tabs-bottom {
  position: relative;
  z-index: 100;
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  margin-top: 0;
  margin-right: auto;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -webkit-flex-direction: column;
  -ms-flex-direction: column;
  flex-direction: column;
  -webkit-box-pack: end;
  -webkit-justify-content: flex-end;
  -ms-flex-pack: end;
  justify-content: flex-end;
  -webkit-box-align: end;
  -webkit-align-items: flex-end;
  -ms-flex-align: end;
  align-items: flex-end;
}

.image-wave{
  position: absolute;
  height: 250px;
  z-index: 0;
  padding: 0;
  margin: 0;
  left: 0;
  top: auto;
  
}

/*Link*/
.works-main-links {
  position: absolute;
  display: flex;
  justify-content: space-between;
  width: 200px;
  height: 100px; 
  left: 0;
  padding-left: 30px;
}

.links-works {
  color: whitesmoke;
}

.links-works:hover {
  border-bottom: 2px solid #C2A43B;
  transition: all 0.2s ease-out;
}

@media (max-width:768px) {
  .image-wave{
    display: none;
  }
.tabs-split {
  display: flex;
}
  .tabs-explanation {
    width: 100%;
    margin-bottom: 5px;
  }
    
  .image-app {
    display: none;
  }

  .works-main-links {
    width: 200px;
    height: auto;
    
  }
  }

